<template>
  <v-app>
    <v-app-bar
      dense
      app
      color="rgba(0,0,0,0)"
      flat
      class="d-flex flex-row justify-center align-middle"
    >
      <v-tabs color="action">
        <v-tab
          v-for="tab in tabOptions"
          :key="tab.value"
          @change="switchPage(tab.value)"
          :ripple="false"
        >
          {{ tab.label }}
        </v-tab>
      </v-tabs>
    </v-app-bar>
    <Top />
    <template v-if="page === 'about'">
      <v-sheet height="300px" />
      <About />
    </template>
    <template v-else-if="page === 'experience'">
      <v-sheet height="300px" />
      <Experience />
      <v-footer>
        <v-main>
          <v-container>
            <v-row>
              <v-col
                cols="12"
                class="d-flex flex-row flex-wrap align-center justify-space-between"
              >
                <p class="text-left body-2 text--secondary ma-0">
                  Fernando Ortiz, 2020
                </p>
                <div>
                  <v-btn
                    color="action"
                    :ripple="false"
                    icon
                    small
                    class="mr-4"
                    href="https://www.linkedin.com/in/fernandoorc"
                  >
                    <feather size="15" type="linkedin"></feather>
                  </v-btn>
                  <v-btn
                    color="action"
                    :ripple="false"
                    icon
                    small
                    class="mr-4"
                    href="https://twitter.com/messages/compose?recipient_id=231578065"
                  >
                    <feather size="15" type="twitter"></feather>
                  </v-btn>
                  <v-btn
                    color="action"
                    :ripple="false"
                    icon
                    small
                    class="mr-4"
                    href="https://www.github.com/FOC96"
                  >
                    <feather size="15" type="github"></feather>
                  </v-btn>
                </div>
              </v-col>
            </v-row>
          </v-container>
        </v-main>
      </v-footer>
    </template>
  </v-app>
</template>

<script>
import Top from "./components/Top";
import About from "./components/About";
import Experience from "./components/Experience";
import { mapState } from "vuex";

export default {
  name: "App",
  title: "Fernando ORC",
  components: {
    Top,
    About,
    Experience
  },

  data: () => ({
    darkOn: false,
    tabOptions: [
      { label: "About", value: "about" },
      { label: "Experience", value: "experience" }
    ]
  }),
  created() {
    document.title = "Fernando ORC";
    this.darkOn = window.matchMedia("(prefers-color-scheme: dark)").matches;
    window
      .matchMedia("(prefers-color-scheme: dark)")
      .addEventListener("change", event => {
        if (event.matches) {
          this.darkOn = true;
        } else {
          this.darkOn = false;
        }
      });
  },
  watch: {
    darkOn() {
      if (this.darkOn) {
        this.$vuetify.theme.dark = true;
      } else {
        this.$vuetify.theme.dark = false;
      }
    }
  },
  computed: {
    ...mapState(["page"])
  },
  methods: {
    switchPage(value) {
      this.$store.commit("setPage", value);
    }
  }
};
</script>
