<template>
  <div>
    <v-card tile flat class="overflowedY" id="msgWrapper">
      <v-main>
        <v-container
          :style="'height: ' + (windowHeight - 230) + 'px;'"
          class="d-flex flex-column justify-end align-end"
          id="scrollableArea"
        >
          <v-card
            class="d-flex flex-column align-end"
            color="transparent"
            flat
            tile
            id="convoWrapper"
          >
            <template v-for="(x, i) in aboutJSON">
              <MessageBubble
                v-if="x.group <= displayedGroup"
                :key="i"
                :text="x.message"
                :image="x.image"
                :sent="!x.sent"
                :file="x.file"
              />
            </template>
            <div class="wholeWidth d-flex flex-row justify-between">
              <span class="caption flex-grow-0 flex-shrink-0" v-if="typing">
                Typing...
              </span>
              <v-spacer></v-spacer>
            </div>
          </v-card>
        </v-container>
      </v-main>
    </v-card>
    <v-card tile flat>
      <v-main>
        <v-container>
          <v-row>
            <v-spacer></v-spacer>
            <v-col
              cols="12"
              :class="
                nextQuestion
                  ? 'd-flex flex-row justify-center align-middle'
                  : 'd-flex flex-row justify-end align-middle'
              "
            >
              <v-btn
                v-if="nextQuestion"
                class="flex-grow-0 flex-shrink-0"
                :ripple="false"
                dark
                color="action"
                rounded
                @click="nextMessage"
              >
                {{ nextQuestion }}
              </v-btn>
              <v-btn
                v-else
                class="flex-grow-0 flex-shrink-0"
                :ripple="false"
                color="action"
                text
                @click="switchPage('experience')"
              >
                Check experience and projects ðŸ‘‰
              </v-btn>
            </v-col>
            <v-spacer></v-spacer>
          </v-row>
        </v-container>
      </v-main>
    </v-card>
  </div>
</template>
<script>
import MessageBubble from "./MessageBubble";
import about from "../assets/about.json";
import { mapActions, mapState } from "vuex";
export default {
  name: "About",
  components: { MessageBubble },
  data() {
    return {
      windowHeight: window.innerHeight,
      aboutJSON: about,
      displayedGroup: 0,
      typing: false,
      questionNum: 0
    };
  },
  computed: {
    ...mapState(["page"]),
    age() {
      const today = new Date();
      let age = Math.floor(
        (today - new Date("November 24, 1996")) / 31557600000
      );

      return age;
    },
    nextQuestion() {
      const questions = this.aboutJSON.filter(x => x.sent && x.message);
      if (this.questionNum < questions.length) {
        return questions[this.questionNum].message;
      } else {
        return null;
      }
    }
  },
  watch: {
    typing() {
      let height = document.getElementById("convoWrapper").clientHeight;
      let scrollableArea = document.getElementById("scrollableArea");
      scrollableArea.scrollTop = height;
    }
  },
  mounted() {
    window.addEventListener("resize", () => {
      this.windowHeight = window.innerHeight;
    });
  },
  methods: {
    ...mapActions({
      switchPage: "setPage"
    }),
    nextMessage() {
      this.displayedGroup += 1;
      this.questionNum += 1;

      setTimeout(() => {
        this.typing = true;
      }, 800);

      setTimeout(() => {
        this.displayedGroup += 1;
        this.typing = false;
      }, 1500);
    }
  }
};
</script>
<style>
.sectionTitle {
  font-size: 1rem;
  font-weight: 600;
  opacity: 0.7;
}
.overflowedY {
  overflow-y: auto;
}
.wholeWidth {
  width: 100%;
}
</style>
